import reactCSS from 'reactcss'

function Cssinjs2(props) {
	const styles = reactCSS({
		'default': {
			box1: {
				width: "300px",
				margin: "30px auto",
				backgroundColor: "#44014C",  //驼峰法
				minHeight: "200px",
				boxSizing: "border-box"
			}
		},
    })
	return (
		<div>
			<div style={ styles.box1 }>容器1</div>
		</div>
	);
}

export default Cssinjs2 