import { CssBox1, CssBox2 } from "./Cssinjs3.js";

function Cssinjs3(props) {
	return (
		<div>
			<div>
				<CssBox1 title="People's Republic of China">c1</CssBox1>
				<CssBox2 color="palevioletred" style={{ color: "pink" }}>c2</CssBox2>
			</div>
		</div>
	);
}

export default Cssinjs3 